import React from "react";
import "boxicons";
import { default as api } from "../store/ApiSlice";

export default function Transaction() {
  const { data, isFetching, isSuccess, isError } = api.useGetLabelsQuery();
  const [deleteTransaction] = api.useDeleteTransactionMutation();
  let Transactions;

  const handleClick = (e) => {
    if (!e.target.dataset.id) return 0;
    deleteTransaction({ _id: e.target.dataset.id });
  };
  if (isFetching) {
    Transactions = <div>Fetching</div>;
  } else if (isSuccess && data) {
    Transactions = data.map((v, i) => (
      <TransactionList
        key={i}
        category={v}
        handler={handleClick}
      ></TransactionList>
    ));
  } else if (isError) {
    Transactions = <div>Error</div>;
  }
  return (
    <div className="flex flex-col py-6 gap-3">
      <h1 className="py-4 font-bold text-xl">Transaction history</h1>
      {Transactions}
    </div>
  );
}

function TransactionList({ category, handler }) {
  if (!category) return null;
  return (
    <div
      className="item flex justify-center bg-gray-50 py-2 rounded-4"
      style={{
        borderRight: `8px solid ${category.color}` ?? "rgb(255, 99, 132)",
      }}
    >
      <button className="px-3" onClick={handler}>
        <box-icon
          data-id={category._id ?? ""}
          color={category.color ?? "rgb(255, 99, 132)"}
          size="16px"
          name="trash"
        ></box-icon>
      </button>
      <span className="block w-full">{category.name ?? ""}</span>
    </div>
  );
}
