import React from "react";
import { default as api } from "../store/ApiSlice";
import { getLabels } from "../helper/Helper";

export default function Labels() {
  const { data, isFetching, isSuccess, isError } = api.useGetLabelsQuery();
  let Transactions;
  if (isFetching) {
    Transactions = <div>Fetching</div>;
  } else if (isSuccess) {
    console.log(getLabels(data, "type"));
    Transactions = getLabels(data, "type").map((v, i) => (
      <LableComponent key={i} data={v}></LableComponent>
    ));
  } else if (isError) {
    Transactions = <div>Error</div>;
  }
  return <>{Transactions}</>;
}

function LableComponent({ data }) {
  if (!data) return <></>;
  return (
    <div className="labels flex justify-between">
      <div className="flex gap-2">
        <div
          className="w-2 h-2 rounded py-3"
          style={{ background: data.color ?? "rgb(255, 99, 132)" }}
        ></div>
        <h3 className="text-md">{data.type ?? ""}</h3>
      </div>
      <h3 className="font-bold">{Math.round(data.percentage) ?? 0}%</h3>
    </div>
  );
}
